import React from 'react';

class TabComp extends React.PureComponent {
  render() {
    const { selected, children } = this.props;
    
    return (
      <div>
        <ul className="inline">
          {children.map((elem, index) => {
            const style = index == this.state.selected ? 'selected' : '';
            return (
              <li className={style} key={index} onClick={this.handleChange(this, index)}> 
                {elem.props.title}
              </li>
            );
          })}
        </ul>
        <div className="tab"> 
          {children[this.state.selected]}
        </div>
        TabComp 
        {selected}
        {children}
      </div>
    );
  }
}

export default TabComp;
